import { Component, OnInit } from '@angular/core';
import { CartRevistaService } from './../cart-revista.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Key } from 'protractor';

@Component({
  selector: 'app-cart-revista',
  templateUrl: './cart-revista.page.html',
  styleUrls: ['./cart-revista.page.scss'],
})
export class CartRevistaPage implements OnInit {
selectedItems = [];
total =0;



  constructor(private cartRevistaService: CartRevistaService) { }  

  ngOnInit() {
    let items = this.cartRevistaService.getCart();
    let selected = {};
    for ( let obj of items ){
      if ( selected[obj.id]){
        selected[obj.id].count++;
      }else{
        selected[obj.id] = { ...obj, count: 1 };
      }
    }
    this.selectedItems = Object.keys(selected).map(Key => selected [Key])
    console.log('items: ', this.selectedItems);
    this.total = this.selectedItems.reduce((a, b) => a + (b.count * b.price), 0);
    
  }

}
